/* RelacoesEcologicasPage.module.css */
.pageContainer {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #eef5db; /* Verde bem claro de fundo */
  color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
}

.pageHeader {
  text-align: center;
  margin-bottom: 30px;
  width: 100%;
}

.pageHeader h1 {
  color: #2c6e49; /* Verde escuro */
  font-size: 2.8em;
  margin-bottom: 5px;
}

.pageHeader p {
  font-size: 1.2em;
  color: #4c956c;
}

.conceptsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 25px;
  width: 90%;
  max-width: 1200px;
  margin-top: 20px;
  margin-bottom: 40px;
  flex-grow: 1;
}

.card {
  background-color: #fff;
  border-radius: 10px;
  padding: 25px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0,0,0,0.15);
}

.cardTitle {
  margin-top: 0;
  font-size: 1.8em;
  color: #2c6e49;
  margin-bottom: 15px;
}

.cardVisual {
  font-size: 4.5em;
  margin-bottom: 15px;
  line-height: 1;
  color: #2c6e49;
  min-height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cardDefinition {
  font-size: 1.1em;
  color: #444;
  margin-bottom: 10px;
  min-height: 60px;
  flex-grow: 1;
  text-align: left;
}

.cardKeywords {
  font-size: 0.95em;
  color: #4c956c;
  font-weight: bold;
  margin-top: auto;
}

.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modalContent {
  background-color: #fefefe;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  width: 70%;
  max-width: 700px;
  position: relative;
  border: 1px solid #888;
  max-height: 90vh;
  overflow-y: auto;
}

.modalTitle {
  color: #2c6e49;
  margin-top: 0;
  border-bottom: 2px solid #4c956c;
  padding-bottom: 10px;
  font-size: 1.8em;
  margin-bottom: 20px;
}

.modalContent :global(.modal-content-custom) p,
.modalContent :global(.modal-content-custom) ul,
.modalContent :global(.modal-content-custom) ol {
  font-size: 1.1em;
  line-height: 1.7;
  text-align: justify;
  color: #333;
  margin-bottom: 1em;
}

.modalContent :global(.modal-content-custom) ul,
.modalContent :global(.modal-content-custom) ol {
  padding-left: 20px;
}

.modalContent :global(.modal-content-custom) strong {
  color: #2c6e49;
  font-weight: bold;
}

.modalContent :global(.modal-content-custom) h4 {
  font-size: 1.2em;
  color: #4c956c;
  margin-top: 20px;
  margin-bottom: 10px;
}

.modalCloseButton {
  color: #aaa;
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 32px;
  font-weight: bold;
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.2s ease;
}

.modalCloseButton:hover,
.modalCloseButton:focus {
  color: #000;
  text-decoration: none;
}

.pageFooter {
  text-align: center;
  padding: 20px;
  margin-top: auto;
  width: 100%;
  background-color: #2c6e49;
  color: #eef5db;
  box-sizing: border-box;
}

.pageFooter p {
  margin: 0;
  font-size: 1em;
}

.legend {
  background-color: #fff;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  margin-bottom: 30px;
  display: inline-block;
}

.legendItem {
  display: inline-block;
  margin-right: 20px;
  font-size: 1.1em;
}

.legendItemPlus {
  color: #28a745; /* Green for benefit */
  font-size: 1.5em;
  margin-right: 5px;
}

.legendItemMinus {
  color: #dc3545; /* Red for harm */
  font-size: 1.5em;
  margin-right: 5px;
}

.legendItemNeutral {
  color: #6c757d; /* Gray for neutral */
  font-size: 1.5em;
  margin-right: 5px;
}

.sectionHeader {
  font-size: 2.2em;
  color: #2c6e49;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 20px;
  width: 100%;
}

.sectionDescription {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}
